:root {
  --text-color-one: #fff;
  --text-color-two: #111;
  --text-color-three: #f87719;
  --background-color-one: #282828;
  --background-color-two: #595959;
  --background-color-three: #b7b7b7;
  --background-color-four: #f8f8f8;
  --background-gradient-one: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --background-gradient-two: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --transition: 250ms linear;
  --hero-gradient320Lib: linear-gradient( 79.49deg, #111 34.1%, rgba(17, 17, 17, 0) 64.67% );
  --hero-gradient768Lib: linear-gradient( 77.77deg, #111 33.58%, rgba(17, 17, 17, 0) 71.57% );
