  --hero-gradient768Home: linear-gradient( 82.55deg, #111 39.6%, rgba(17, 17, 17, 0) 72.95% );
  --hero-gradient1280Home: linear-gradient( 83.16deg, #111 36.85%, rgba(17, 17, 17, 0) 60.05% );
}

