.library {
    padding-top: 96px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 20px;
    padding-right: 20px;
}

@media screen and(min-width: 768px) {
    .library {
        padding-top: 140px;
        padding-left: 32px;
        padding-right: 32px;
        width: 768px;
    }
}

@media screen and(min-width: 1280px) {
    .library {
        padding-top: 176px;
        width: 1280px;
    }
}

@media screen and(max-width: 767px) {
.library-container {
    margin-right: auto;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
}

@media screen and(min-width: 768px) {
    .library-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 768px; 
    }
}

@media screen and(min-width: 1280px) {
    .library-container {
        margin-right: auto;
        margin-left: auto;
        width: 1280px;
    }
}

.library-container-item {
    margin-bottom: 16px;
}

.library-container-item:nth-last-child(-n + 1) {
    margin-bottom: 0;
}

@media screen and(min-width: 768px) {
    .library-container-item {
        max-width: calc((100% - 64px) / 3);
        margin-left: 0px;
        margin-right: 16px;
        margin-bottom: 28px;
    }
    .library-container-item:nth-child(3n) {
        margin-right: 0px;
    }
}

.library-container-img {
    position: relative;
    width: 280px;
    height: 406px;
    background-size: cover;
    background-position: center;
}

@media screen and (min-width: 768px) {
    .library-container-img {
        width: 224px;
        height: 325px;
    }
}

@media screen and (min-width: 1280px) {
    .library-container-img {
        width: 394px;
        height: 574px;
    }
}

.library-container-about {
    position: absolute;
    left: 12px;
    bottom: 12px;

    font-weight: 500;
    font-size: 12px;
    line-height: 1.65;

    color: #ffffff;
}

.rating-library {
    position: absolute;
    right: 12px;
    bottom: 16px;
}